export const convertTime = () => {
    const date = new Date();
    return date.toUTCString();
};

export const convertText = (type) => {
    let Text = '';
    if (type === 'wedding') {
        Text = '';
    } else if (type === 'ordination') {
        Text = '';
    } else if (type === 'housewarming') {
        Text = '';
    }

    return Text;
};

