import * as yup from 'yup';
import moment from 'moment';

export const marrySchema = yup.object().shape({
    hostOne: yup.string().required(''),
    hostTwo: yup.string().required(''),
    phone: yup
        .string()
        .required('')
        .min(10, ' 10 ')
        .max(10, ' 10 '),
    date: yup
        .date()
        .min(moment(), '')
        .required('')
        .nullable(),
    locationName: yup.string().required('')
});
